export default class Gate {

    constructor(user) {
        this.user = user;
    }

    isAdmin() {
        return this.user.roles[0].name === '';
    }

    isUser() {
        return this.user.roles[0].name === '';
    }

    isAdminOrUser() {
        if (this.user.type === 'user' || this.user.type === 'admin') {
            return true;
        }
    }
}